:root {
  --background-color: #0c0c0c;
  --white-color: #fff;
  --line-color: #474840;
  --accent-background: #706143;
  --light-background: #121212;
  --element-color: #3a3a3a;
  --text-color: #ddd;
  --white-color: #fff;
  --background-btn: #292929;
  --hover-color: #ad9445;
  --accent-color: #bd9e34;
  --line-color: #bb9e36;
}

